import React from "react"
import { Layout, Section, Card, Heading, Button } from "components"
import { Row, Col } from "antd"
import { useLocation } from "react-router"
import { Trans } from "@lingui/macro"
import { Link } from "react-router-dom"

export default function Acceptance() {
	const location = useLocation()
	const params = new URLSearchParams(location.search)
	const isAccepted = params.get("accept") && params.get("accept") === "true"
	const isError = params.get("error") && params.get("error") === "true"
	const acceptMessage = {
		heading:
			(isError && <Trans>Oops! Something bad happened</Trans>) ||
			(isAccepted && <Trans>Great! This is very you!</Trans>) ||
			(!isAccepted && <Trans>You rejected it :(</Trans>),
		text:
			(isError && !isAccepted && params.get("message")) ||
			(isAccepted && <Trans>You have accepted the assignment. Wait until the student reaches you</Trans>) ||
			(!isAccepted && (
				<Trans>You have rejected the assignment. That's okay. Probably you'll get clicked with other future assignments.</Trans>
			))
	}

	return (
		<Layout>
			<Section centered>
				<Row type="flex" justify="center">
					<Col lg={12}>
						<Card autoHeight noHover>
							<Heading level={4} content={acceptMessage.heading} subheader={acceptMessage.text} marginBottom="2em" />
							<Link to="/">
								<Button type="primary" icon="home">
									&nbsp; <Trans>Go to Home</Trans>
								</Button>
							</Link>
						</Card>
					</Col>
				</Row>
			</Section>
		</Layout>
	)
}
